.appPromotion {
  max-width: 320px;
  flex: 1;
  /* background: #fff; */
  font-size: 1.2em;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 0 25px;
}
.content {
  display: flex;
  flex-direction: column;
  background: #fff;
  padding: 20px;
  border-radius: 5px;
}
.content span {
  font-size: 0.8em;
  padding: 20px 0;
}

.promoImg {
  width: 80%;
  margin-top: 20px;
  border-radius: 5px;
}

@media screen and (max-width: 1200px) {
  .appPromotion {
    margin-left: 50px;
    margin-right: 0;
  }
}
