.contentBoxOne {
  min-width: 250px;
  max-width: 300px;
  flex: 1;
  background: #fff;
  padding: 20px;
  font-size: 1.2em;
  border-radius: 5px;
  display: flex;
  margin-left: 25px;
  flex-direction: column;
}

.Wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0;
  font-weight: 500;
  line-height: 110%;
}
.item {
  display: flex;
  flex-direction: column;
  font-size: 0.6em;
  flex-wrap: wrap;
  margin-top: 10px;
}
.item img {
  max-width: 130px;
  height: 100px;
  object-fit: cover;
  border-radius: 2px;
}
.Link {
  color: rgb(0, 123, 123);
  text-decoration: none;
  font-size: 0.7em;
  font-weight: 600;
}
.Link:hover {
  /* font-weight: 800; */
  text-decoration: underline;
}

@media screen and (max-width: 1200px) {
  .contentBoxOne {
    margin: 0 25px;
  }
}
