.searchbarContainer {
  display: flex;
  padding: 0 10px;
  flex: 1;
}
.searchCategory {
  width: 60px;
}
.searchCategory select {
  width: 100%;
  padding: 0 10px;
  height: 40px;
  border-radius: 5px 0 0 5px;
  background-color: #e9e9e9;
  border: 1px solid;
  border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.2) rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.searchCategory select:hover {
  background-color: #d4d4d4;
}

.searchbar {
  border: none;
  padding: 0 10px;
  flex: 1;
  outline: none;
}

.searchIcon {
  padding: 0 8px;
  border: none;
  border-radius: 0 5px 5px 0;
  background: #febd68;
  outline: none;
  cursor: pointer;
}

.searchbar:active .searchbarContainer {
  border: 2px solid #febd68;
}
