.home {
  width: 100%;
  min-width: 1200px;
  max-width: 1500px;
  margin: 0 auto;
}
.mainImg {
  z-index: -1;
  margin-bottom: -250px;
  height: 500px;
  background: url("https://m.media-amazon.com/images/I/613RXVVZrSL._SX3000_.jpg");
  overflow: hidden;
  background-position-x: center;
  background-size: 100%;
  background-repeat: no-repeat;
  mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.9),
    rgba(0, 0, 0, 0.8),
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0)
  );
}
.contentWrapper {
  z-index: 1;
  position: relative;
  left: 0;
  /* padding: 20px; */
  width: 100%;
  min-width: 1200px;
  max-width: 1500px;
  display: flex;
  justify-content: space-evenly;
  /* align-items: center; */
}

@media screen and (max-width: 1350px) {
  .AppPromotions {
    display: none;
  }
}
