.deals {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.title {
  font-weight: 700;
  font-size: 1.5em;
}
.dealWrapper {
  flex: 1;
  margin: 0 25px;
  background-color: #fff;
  border-radius: 5px;
  padding: 25px;
}

.items {
  min-width: 100px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  /* background: #e3e3e3; */
  cursor: pointer;
  border-radius: 5px;
  padding: 15px 15px;
  margin-top: 30px;
}
.items:hover {
  box-shadow: 20px 20px 40px #d9d9d9, -20px -20px 40px #ffffff;
  /* transition: margin 0.2s ease-in-out; */
}
.items .imgContainer {
  width: 100%;
  height: 170px;
  background: #fff;
  overflow: hidden;
  display: flex;
  align-items: center;
  border-radius: 5px;
}
.imgContainer img {
  height: 79%;
  margin: 0 auto;
  /* width: 100%; */
}
.itemDetails {
  background: #fff;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.itemTitle {
  font-size: 1em;
  font-weight: 700;
}
.itemPrice {
  font-size: 0.8em;
  font-weight: 300;
  margin-top: 5px;
}
.itemRating {
  margin-top: 10px;
}
.itemRating img {
  width: 25px;
  margin: 0 -3px;
}
.addToCart {
  display: flex;
  margin-top: 10px;
  justify-content: space-between;
}
.addToCart button {
  padding: 5px;
  border-radius: 2px;
  font-weight: 600;
  font-size: 0.7em;
  cursor: pointer;
  border: none;
  background: #fca400;
}
