.header {
  background: #131921;
  height: 60px;
  min-width: 1200px;
  margin: 0;
  padding: 0;
  position: sticky;
  top: 0;
  z-index: 100;
}

.headerContainer {
  display: flex;
  color: #fff;
  height: 60px;
  align-items: center;
  /* justify-content: space-between; */
  font-size: 20px;
}

.headerContainer .headerLogo img {
  height: 35px;
  padding: 18px 10px 5px 15px;
}

.optionTwo {
  font-size: 0.75em;
  padding: 0px 15px;
  display: flex;
  flex-direction: column;
}
