.contentBoxTwo {
  background: #fff;
  min-width: 250px;
  max-width: 400px;
  flex: 1;
  padding: 20px;
  border-radius: 5px;
  font-size: 1.2em;
  margin-left: 25px;
}
.contentBoxTwo img {
  width: 100%;
  height: 70%;
  padding: 20px 0;
  border-radius: 2px;
}

.link {
  color: rgb(0, 123, 123);
  text-decoration: none;
  font-size: 0.7em;
  font-weight: 600;
}
.link:hover {
  /* font-weight: 800; */
  text-decoration: underline;
}

@media screen and (max-width: 1200px) {
  .contentBoxTwo {
    margin: 0;
  }
}
